.FinishButton{
    display: flex;
    flex-grow: 0;
    align-items: center;
    justify-content: center;
    transition: 0.15s;
    font-size: 14px;
    padding: 8px 16px ;
    border: none;
    background-color: #7c3aed;
    color: white;
    border-radius: 16px;
}
.FinishButton:hover{
    transition: 0.15s;
    background-color: #9b69f1;
    color: white;
}