.PageContent{
    margin: 10px;
    display: flex;
    flex-direction: row;
    flex-grow: 1;
    gap: 10px;
}
.LeftCluster{
    display: flex;
    flex-direction: column;
    flex-grow: 2;
    gap: 12px;
    
}

.Graph{
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 32px;
}

.FinishedTasks{
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    gap: 12px;
    overflow-y:scroll;
}

.CardContent{
    padding: 12px;
    display: flex;
    flex-direction: column;
    gap:12px;
}

.SecondLayer{
    display: flex;
    justify-content: flex-end;
}

.FinishedTasksTitle{
    font-size: 24px;
    font-weight: bold;
}

.Title{
    height: fit-content;
    font-size: 48px;
    font-weight: bold;
    padding: 24px;
}

.CardGraphContent{
    display: flex;
    flex-grow: 1;
    align-items: center;
    justify-content: center;
}

.description{
    width: 256px;
    text-wrap: true;
}

.Loading{
    font-weight: bold;
    color: grey;
    font-size: 48px;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
}
